import "./charInfo.scss";
// import thor from "../../resources/img/thor.jpeg";

import { Component } from "react";
import PropTypes from "prop-types";

import MarvelService from "../../services/MarvelService";
import Skeleton from "../skeleton/Skeleton";
import Spinner from "../spinner/Spinner";

import ErrorMess from "../errorMess/ErrorMess";

class CharInfo extends Component {
  marvelService = new MarvelService();
  state = {
    char: null,
    loading: false,
    error: false,
  };

  onCharLoaded = (char) => {
    this.setState({ char, loading: false });
  };

  onLoading = () => {
    this.setState({ loading: true, error: false });
  };

  onError = () => {
    this.setState({ loading: false, error: true });
  };

  updateCharacter = () => {
    const { charId } = this.props;
    if (!charId) {
      return;
    }
    this.onLoading();
    this.marvelService
      .getCharacter(charId)
      .then(this.onCharLoaded)
      .catch(this.onError);
  };

  componentDidMount() {
    this.updateCharacter();
  }
  componentDidUpdate(prevProps) {
    if (this.props.charId !== prevProps.charId) {
      this.updateCharacter();
    }
  }

  render() {
    const { char, loading, error } = this.state;

    const spiner = loading ? <Spinner /> : null;
    const errMess = error ? <ErrorMess /> : null;
    const charLoaded = char ? <Viev char={char} /> : null;
    const content = spiner || errMess || charLoaded || <Skeleton />;

    return <div className="char__info">{content}</div>;
  }
}

const Viev = ({ char }) => {
  const { name, description, thumbnail, homepage, wiki, comics } = char;
  let imgStyle = { objectFit: "cover" };
  if (
    thumbnail ===
      "http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg" ||
    thumbnail ===
      "http://i.annihil.us/u/prod/marvel/i/mg/f/60/4c002e0305708.gif"
  ) {
    imgStyle.objectFit = "contain";
  }

  return (
    <>
      <div className="char__basics">
        <img src={thumbnail} style={imgStyle} alt="name" />
        <div>
          <div className="char__info-name">{name}</div>
          <div className="char__btns">
            <a href={homepage} className="button button__main">
              <div className="inner">homepage</div>
            </a>
            <a href={wiki} className="button button__secondary">
              <div className="inner">Wiki</div>
            </a>
          </div>
        </div>
      </div>
      <div className="char__descr">
        {description ? (
          description
        ) : (
          <i>There is no description for this character :(</i>
        )}
      </div>
      <div className="char__comics">Comics:</div>
      <ul className="char__comics-list">
        {comics.length > 0 ? (
          comics.slice(0, 10).map((item, index) => {
            return (
              <li className="char__comics-item" key={index}>
                {item.name}
              </li>
            );
          })
        ) : (
          <i>There is no comics for this character :(</i>
        )}
      </ul>
    </>
  );
};
