import "./charList.scss";
// import abyss from "../../resources/img/abyss.jpg";

import { Component } from "react";
import PropTypes from "prop-types";

import MarvelService from "../../services/MarvelService";
import Spinner from "../spinner/Spinner";
import ErrorMess from "../errorMess/ErrorMess";

class CharList extends Component {
  marvelService = new MarvelService();
  state = {
    chars: [],
    loading: true,
    error: false,
    offset: this.marvelService._baseCharsOffset,
    limit: this.marvelService._baseCharsLimit,
    newItemLoading: false,
    charEnded: false,
    myRef: [],
  };

  onCharsLoading = () => {
    this.setState({ newItemLoading: true });
  };

  onCharsLoaded = (newChars) => {
    let ended = false;
    if (newChars.length < this.state.limit) {
      ended = true;
    }

    // console.log()
    // localStorage.setItem('limit', this.state.limit + 3)
    // localStorage.setItem('offset', this.state.offset + 3)
    this.setState(({ chars, offset, limit }) => ({
      chars: [...chars, ...newChars],
      loading: false,
      offset: offset + limit,
      newItemLoading: false,
      charEnded: ended,
    }));

    // console.log(this.state.chars)
  };

  setMyRef = (element) => {
    this.state.myRef.push(element);
  };

  onError = () => {
    this.setState({ error: true, loading: false });
  };

  updateCharacters = () => {
    this.onCharsLoading();

    this.marvelService
      .getAllCharacters(this.state.limit, this.state.offset)
      .then((charArr) => {
        const charItems = charArr.map(({ name, thumbnail, id }) => {
          // console.log(id)
          const imageStyle = { objectFit: "cover" };
          if (
            thumbnail ===
              "http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg" ||
            thumbnail ===
              "http://i.annihil.us/u/prod/marvel/i/mg/f/60/4c002e0305708.gif"
          ) {
            imageStyle.objectFit = "contain";
          }
          return (
            <li
              ref={this.setMyRef}
              tabIndex={0}
              //char__item_selected
              className="char__item"
              key={id}
              onKeyDown={(e) =>
                e.key === "Enter"
                  ? this.props.onChangeId(e, id, this.state.myRef)
                  : null
              }
              onClick={(e) => this.props.onChangeId(e, id, this.state.myRef)}
            >
              <img src={thumbnail} style={imageStyle} alt={`${name} photo`} />
              <div className="char__name">{name}</div>
            </li>
          );
        });

        this.onCharsLoaded(charItems);
      })
      .catch(this.onError);
  };

  componentDidMount() {
    // console.log(this.marvelService._baseCharsOffset);
    this.updateCharacters();
  }

  //console.log
  render() {
    const { chars, loading, error, newItemLoading, charEnded } = this.state;

    const err = error ? <ErrorMess /> : null;
    const spinner = loading ? <Spinner /> : null;
    const content = spinner || err || <ul className="char__grid">{chars}</ul>;
    return (
      <div className="char__list">
        {content}

        <button
          className="button button__main button__long"
          disabled={newItemLoading}
          style={{ display: charEnded ? "none" : "block" }}
          onClick={this.updateCharacters}
        >
          <div className="inner">load more</div>
        </button>
      </div>
    );
  }
}

CharList.propTypes = {
  onChangeId: PropTypes.func.isRequired,
};

export default CharList;
