import "./randomChar.scss";
import thor from "../../resources/img/thor.jpeg";
import mjolnir from "../../resources/img/mjolnir.png";
import { Component } from "react";
import MarvelService from "../../services/MarvelService";
import Spinner from "../spinner/Spinner";
import ErrorMess from "../errorMess/ErrorMess";
//src="http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg"
class RandomChar extends Component {
  marvelService = new MarvelService();
  state = {
    char: {},
    loading: true,
    error: false,
  };

  onCharLoaded = (char) => {
    this.setState({ char, loading: false });
  };
  onCharLoading = () => {
    this.setState({ loading: true, error: false });
  };

  onError = () => {
    this.setState({ loading: false, error: true });
  };

  updateCharacter = () => {
    this.onCharLoading();
    const id = Math.floor(Math.random() * (1011600 - 1011000) + 1011000);
    this.marvelService
      .getCharacter(id)
      .then(this.onCharLoaded)
      .catch(this.onError);
  };

  componentDidMount() {
    this.updateCharacter();
    // this.intervalId = setInterval(this.updateCharacter, 3000)
  }

  componentWillUnmount() {
    // clearInterval(this.intervalId)
  }
  render() {
    const { char, loading, error } = this.state;

    const err = error ? <ErrorMess /> : null;
    const spinner = loading ? <Spinner /> : null;
    const content = spinner || err || <View charObj={char} />;

    return (
      <div className="randomchar">
        {content}
        <div className="randomchar__static">
          <p className="randomchar__title">
            Random character for today!
            <br />
            Do you want to get to know him better?
          </p>
          <p className="randomchar__title">Or choose another one</p>
          <button
            className="button button__main"
            onClick={this.updateCharacter}
          >
            <div className="inner">try it</div>
          </button>
          <img src={mjolnir} alt="mjolnir" className="randomchar__decoration" />
        </div>
      </div>
    );
  }
}

const View = ({ charObj }) => {
  const { name, description, thumbnail, homepage, wiki } = charObj;
  let imgStyle = { objectFit: "cover" };
  if (
    thumbnail ===
      "http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg" ||
    thumbnail ===
      "http://i.annihil.us/u/prod/marvel/i/mg/f/60/4c002e0305708.gif"
  ) {
    imgStyle.objectFit = "contain";
  }

  return (
    <div className="randomchar__block">
      <img
        src={thumbnail}
        alt="Random character"
        className="randomchar__img"
        style={imgStyle}
      />
      <div className="randomchar__info">
        <p className="randomchar__name">{name}</p>
        <p className="randomchar__descr">
          {description ? (
            description.length > 220 ? (
              `${description.slice(0, 210)}...`
            ) : (
              description
            )
          ) : (
            <i>There is no description for this character :(</i>
          )}
        </p>
        <div className="randomchar__btns">
          <a href={homepage} className="button button__main" target="_blank">
            <div className="inner">homepage</div>
          </a>
          <a href={wiki} className="button button__secondary" target="_blank">
            <div className="inner">Wiki</div>
          </a>
        </div>
      </div>
    </div>
  );
};

export default RandomChar;
