class MarvelService {
  _apiBase = "https://gateway.marvel.com:443/v1/public/";
  _apiKey = "apikey=858959d886a461b3a569f668a89b39e5";
  _baseCharsOffset = 210;
  _baseCharsLimit = 9;

  getResource = async (url) => {
    const res = await fetch(url);
    if (!res.ok) {
      throw new Error(`Could not fetch ${url}. Status: ${res.status}`);
    }
    return await res.json();
  };

  getAllCharacters = async (limit, offset) => {
    const res = await this.getResource(
      `${this._apiBase}characters?limit=${limit}&offset=${offset}&${this._apiKey}`
    );
    return res.data.results.map(this._transformCharacter);
  };

  getCharacter = async (id) => {
    const res = await this.getResource(
      `${this._apiBase}characters/${id}?${this._apiKey}`
    );

    return this._transformCharacter(res.data.results[0]);
  };

  _transformCharacter = (charObj) => {
    return {
      id: charObj.id,
      name: charObj.name,
      description: charObj.description,
      thumbnail: charObj.thumbnail.path + "." + charObj.thumbnail.extension,
      homepage: charObj.urls[0].url,
      wiki: charObj.urls[1].url,
      comics: charObj.comics.items,
    };
  };
}
export default MarvelService;
