import { Component } from "react";
// import ReactDOM from "react-dom";

import AppHeader from "../appHeader/AppHeader";
import RandomChar from "../randomChar/RandomChar";
import CharList from "../charList/CharList";
import CharInfo from "../charInfo/CharInfo";
import ErrorBoundary from "../errorBoundary/ErrorBoundary";

import decoration from "../../resources/img/vision.png";



class App extends Component {
  state = {
    selectedChar: null,
  };

  onCharSelected = (e, selectedChar, ref) => {
    this.setState({ selectedChar });
    // console.log(e.currentTarget);
    // console.log(selectedChar);
    ref.forEach((item) => {
      item.classList.remove("char__item_selected");
      if (item === e.currentTarget) {
        item.classList.add("char__item_selected");
      }
    });
    // console.log(ref[0] === e.currentTarget);
    // console.log(this.state.selectedChar)
  };

  render() {
    return (
      <div className="app">
       

        <ErrorBoundary>
          <AppHeader />
        </ErrorBoundary>
        <main>
          <RandomChar />
          <div className="char__content">
            <ErrorBoundary>
              <CharList onChangeId={this.onCharSelected} />
            </ErrorBoundary>
            <ErrorBoundary>
              <CharInfo charId={this.state.selectedChar} />
            </ErrorBoundary>
          </div>
          <img className="bg-decoration" src={decoration} alt="vision" />
        </main>
      </div>
    );
  }
}

export default App;
